.table {
  display: grid;
  grid-template-columns: 1fr 1fr;
  width: fit-content;
}

.table-text {
  font-size: 1.8rem;
  line-height: 1.8;
  font-weight: 500;
}

.table-cell {
  border: 1px solid #ffffff;
}

.table-cell > * {
  padding: 0.2rem 2.4rem;
}

.table-header {
  background-color: rgba(255, 255, 255, 0.3);
}

.table-highlight {
  background-color: rgba(255, 255, 255, 0.1);
}
