/*

----- Typography system -----
Font size system (px)
10 / 12 / 14 / 16 / 18 / 20 / 24 / 30 / 36 / 44 / 52 / 62 / 74 / 86 / 98

----- Whitespace -----
Spacing system (px)
2 / 4 / 8 / 12 / 16 / 24 / 32 / 48 / 64 / 80 / 96 / 128

*/

* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

html {
  font-size: 62.5%;
  font-family: "Montserrat", sans-serif;
  background-color: #000000;
}

img {
  display: block;
}

.heading-primary,
.heading-secondary,
.heading-tertiary {
  font-weight: 700;
  /* color: #ffffff; */
  letter-spacing: -0.5px;
}

.heading-primary {
  font-size: 6.2rem;
  line-height: 1.2;
  letter-spacing: -3px;
  font-weight: 700;
  text-align: center;
  margin-bottom: 2.4rem;
}

.heading-secondary {
  font-size: 3rem;
  /* text-align: center; */
  /* margin-bottom: 2.4rem; */
}

.heading-tertiary {
  font-size: 2.4rem;
}

.link:link,
.link:visited {
  display: inline-block;
  color: #ffffff;
  text-decoration: none;
  border-bottom: 1px solid currentColor;
  padding-bottom: 2px;
  transition: all 0.3s;
}

.link:hover,
.link:active {
  border-bottom: 1px solid transparent;
}

.link-endpoint:link,
.link-endpoint:visited {
  display: inline-block;
  color: #ffffff;
  text-decoration: none;
  padding-bottom: 2px;
  transition: all 0.3s;
}

.f_white {
  color: #ffffff;
}

.margin-top_sm {
  margin-top: 3.2rem;
}

.margin-bottom_none {
  margin-bottom: 0 !important;
}

.margin-bottom_sm {
  margin-bottom: 2.4rem;
}

.margin-bottom_md {
  margin-bottom: 3.2rem;
}

.margin-bottom_big {
  margin-bottom: 8.2rem;
}

.center {
  text-align: center;
}

.text {
  font-size: 1.8rem;
  line-height: 1.8;
  font-weight: 500;
  margin-bottom: 0.9rem;
}

.text-sm {
  font-size: 1.4rem;
  line-height: 1.2;
  margin-bottom: 0.4rem;
}

.table {
  display: grid;
  grid-template-columns: 1fr 1fr;
  width: fit-content;
}

.table-text {
  font-size: 1.8rem;
  line-height: 1.8;
  font-weight: 500;
}

.table-cell {
  border: 1px solid #ffffff;
}

.table-cell > * {
  padding: 0.2rem 2.4rem;
}

.table-header {
  background-color: rgba(255, 255, 255, 0.3);
}

.table-highlight {
  background-color: rgba(255, 255, 255, 0.1);
}

.table-light {
  background-color: rgba(255, 255, 255, 0.7);
}

.text-wrap {
  display: inline-block;
  width: 100%;
  overflow-x: auto;
}

.snippet {
  background-color: rgba(255, 255, 255, 0.7);
  color: #000000;
  padding: 0.1rem 0.3rem;
}

.get {
  font-weight: 700;
  color: #000000;
  background-color: #00ff00;
  padding: 0.1rem 0.3rem;
  border-radius: 10px;
}
